.landing {
    height: 90vh;
    background-image: url(https://github.com/Heliosoto1987/noname/blob/master/public/assets/noname.jpeg?raw=true);
    background-repeat: no-repeat;
    background-position-x: center;
    background-position-y: center;
    display: flex;
    justify-content: center;
    background-color: black;
    background-size: 200px;
}

.landing__title{
    padding-top: 10%;
    color: white;
    font-style: italic;
    font-size: 3rem;
}

.lading__rosters{   
    background-image: url(https://www.egames.news/__export/1628024712921/sites/debate/img/2021/08/03/codigos_call_of_duty_mobile_1.jpg_1902800913.jpg);
    background-size: cover;
    background-position: center;
    
}

.lading__rosters--title{
    text-align: center;
    font-size: 2.5rem;
}

.lading__rosters--div{
    font-weight: bold;
    background-repeat: no-repeat;
    margin-bottom: 20px;
}

.lading__rosters--ul{
    margin-top: 5vh;
    margin-left: 10vw;
    font-size: 2rem;
}

.lading__rosters--li{
    margin: 2px;
}